<template>
  <div id="app">
  <Header />

  <div class="wrap">
   <div class="search">
      <input type="text" v-model="user" class="searchTerm" placeholder="Search or jump to...">
      <button type="submit" v-on:click="findUsers" class="searchButton">
        <i class="fa fa-search"></i>
     </button>
   </div>
</div>


  <searchBox />


  <div class="seachBox">
    <p>Hello ji</p>
  </div>
    
  </div>
</template>

<script>
import Header from './components/layouts/Header';
import SearchBox from '../src/components/SearchBox';
import axios from 'axios';

export default {
  name: 'App',

  components: {
    Header,
    SearchBox,
  },
  props:{
    userName : this.user
  },
  data (){
    return {
      user : ''
    }
  },
  methods : {
    findUsers(){
      let url = 'https://api.github.com/search/users?q=' + this.user;
      axios.get(url)
      .then(function (res){
        window.open(res.data.items[0].html_url,"_blank");
      })
      .catch(err => console.log(err,"user not found"));
    }

    }
  
  
}
</script>

